@import "variables.scss";

.showcase_container{
    width:100rem;height:50rem;display:flex;align-self: center;flex-direction: column;
    margin-top:-11rem;background-color: white;border-radius: 0.8rem;border:1px solid #eee;
    box-shadow: 7px 10px 16px 0px rgba(0,0,0,0.18);
    &_title{
        color:white;font-size: 3.5rem;align-self: center;color:$primaryColor;font-family:eurostile, sans-serif;
    }
}
.showcase_grid{
    height:40rem;padding:2rem;display:flex;flex-direction: row;justify-content: space-around;
}
.skill_item{
    border:1px solid #eee;width:30rem;height:30rem;display:flex;box-shadow: 7px 10px 16px 0px rgba(0,0,0,0.05);border-radius: 0.8rem;
    flex-direction: column;
    &_title{
        font-size: 2rem;align-self: center;margin-bottom:0rem;margin-top:0.8rem;font-family:eurostile, sans-serif;
    }
    &_skills{
        overflow:hidden;padding:1rem;display:flex;gap:1rem;flex-wrap:wrap;align-items:center;justify-content:center;
    }
}

.skill_item_right{
    width:30rem;height:30rem;display:flex;justify-content:space-between;flex-direction:column;
    &_rightcards{
        border:1px solid #eee;width:30rem;height:14rem;display:flex;box-shadow: 7px 10px 16px 0px rgba(0,0,0,0.05);
    padding-bottom: 0;border-radius: 0.8rem;
    flex-direction: column;
    }
}