.skill_item_skills_imgcontainer{
    border:1px solid #eee;width:8rem;height:6.5rem;flex-direction:column;display:flex;align-items:center;padding-top:1rem;
    box-shadow: 7px 10px 16px 0px rgba(0,0,0,0.04);justify-content: center;
    &_text{
        margin-bottom:-1rem;margin-top:0rem;align-self: center;font-family:eurostile, sans-serif;
    }
    &_image{
        width:4rem;height:4rem;margin-bottom:-1rem;
    }   
    &_imagetitle{
        font-size:1.5rem;font-family:eurostile, sans-serif;
    }
}